body{
  background-color:rgb(219, 57, 57);
  color:rgb(143, 207, 14);
}

main{
  padding-top: 50px;
  max-width: 340px;
  margin: 0 auto ;
  text-align: center;
}

button{
  background-color: transparent;
  color:rgba(255,255,255,0.8) ;
  border: 0;
  display: inline-block;
  width: 100px;
  cursor:pointer;
}


button.with-text{
width: auto;
background-color: rgba(70, 43, 43, 0.8) ;
border-radius: 10px;
padding: 10px 20px ;
font-size: 1.4rem;
line-height: 36px;
}

button.with-text svg{
  width: 36px;
  float: left;
  margin-right: 5px;
}